
.SDButton {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.SDButton:active {
    position: absolute;
    top: 5%;
    left: 5%;
    width: 90%;
    height: 90%;
}


.SDButton-content {
    position: absolute;
    top: 5%;
    left: 5%;
    width: 90%;
    height: 90%;
    background: rgb(34, 34, 34);
    border-radius: 15%;
    
}


.SDButton-conainer {
    position: absolute;
    top: 1%;
    left: 1.5%;
    width: 97%;
    height: 97%;
    background: black;
    border-radius: 15%;
    
}


.SDButton-border-wrap {
    top: 5%;
    left: 5%;
    width: 90%;
    height: 90%;
 
    position: relative;
    background: linear-gradient(to bottom, #555555, #212121);
    
    border-radius: 15%;
  }

  .reflect-top-left {
      position: absolute;
      width: 25%;
      height: 8%;
      left: 20%;
      top:1%;
      background-color: rgb(255, 255, 255);
      border-top-left-radius: 20%;
      border-bottom-left-radius: 100%;
      opacity: 20%;
    
  }
  .reflect-top {
    position: absolute;
    width: 10%;
    height: 8%;
    left: 45%;
    top:1%;
    background-color: rgb(255, 255, 255);
    opacity: 20%;
  
}
.reflect-top-right {
    position: absolute;
    width: 25%;
    height: 8%;
    left: 55%;
    top:1%;
    background-color: rgb(255, 255, 255);
    border-top-right-radius: 20%;
    border-bottom-right-radius: 100%;
    opacity: 20%;
  
}


.reflect-bottom-left {
    position: absolute;
    width: 25%;
    height: 8%;
    left: 20%;
    top:91%;
    background-color: rgb(255, 255, 255);
    border-bottom-left-radius: 20%;
    border-top-left-radius: 100%;
    opacity: 20%;
  
}
.reflect-bottom {
  position: absolute;
  width: 10%;
  height: 8%;
  left: 45%;
  top:91%;
  background-color: rgb(255, 255, 255);
  opacity: 20%;

}
.reflect-bottom-right {
  position: absolute;
  width: 25%;
  height: 8%;
  left: 55%;
  top:91%;
  background-color: rgb(255, 255, 255);
  border-bottom-right-radius: 20%;
  border-top-right-radius: 100%;
  opacity: 20%;

}

